@media screen and (orientation: portrait) 
{
    .sizes 
    {
        display: flex;
        flex-direction: column;
        position: fixed;
        width: 100vw;
        height: 100vh;
        text-align: center;
        padding-top: auto;
        padding-bottom: auto;
        z-index: 1010;
        align-content: center;
        justify-content: center;
        justify-items: center;
        align-items: center;
    }
    .sizes img {
        height: 10vh;
        width: 10vw;
    }
}
@media screen and (orientation: landscape) {
    .sizes 
    {
        display: none;
    }
}
