/*------------- intro container ---------------------- */
* {
  box-sizing: border-box;
}
.intro-container
{
    background-color: #FDF6D8;
    border-color: #e0dbc2;
    border-radius: 30px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    
    padding: 1.5vw;
    
    margin: auto;
    margin-top: 2vw;
    margin-bottom: 2vw;

    position: relative;
    max-width: 70vw;
    /*max-height: 15rem;*/

    display: flex;
    list-style: none;
    
  
    /*border: 1px solid rgb(255 255 255 / 5%);*/
    border-radius: 1.5rem;
    
}
.intro-container img{
    border-radius: 100px;
    /*max-width:10rem;*/
    justify-self: center;
    align-self: center;

    max-height: 20vw;
    aspect-ratio: 1.8 / 2.5;
}
.intro-container ul{
    display: flex;
    flex-direction: column;

    align-items: right;
    justify-content: center;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

    text-align: justify;
    max-width: 60vw;
    padding: 2vw;
    /*margin: 3vw;*/
}
.intro-container ul p{
    color: #312f2f;
    font-size: 1.5vw;
}
.intro-container ul h2{
    font-size: 2vw;
}


.intro-container:after,
.intro-container:before {
  content: "";
  height: 15vh;
  width: 0.1vw;
  position: absolute;
  background: linear-gradient(
    transparent,
    #B32305,
    transparent
  );
  opacity: 0;
}

.intro-container:after {
  top: 85%;
  right: 0px;
  transition: top 600ms ease, opacity 600ms ease;
  transition-property: top, opacity;
}

.intro-container:hover:after {
  top: 15%;
  opacity: 1;
}

.intro-container:before {
  top: 0%;
  left: 0.5px;
  transition: top 600ms ease, opacity 600ms ease;
  transition-property: top, opacity;
}

.intro-container:hover:before {
  top: 65%;
  opacity: 1;
}


